import React, { Component } from "react"
import { importAllFiles } from "../helpers/functions"
import GalleryTemplate from "../templates/GalleryTemplate"

class GardenArchitecture extends Component {
  state = {
    photos: importAllFiles(
      require.context(
        "../assets/photos/gardenArchitecture",
        false,
        /\.(png|jpe?g|svg)$/
      )
    ),
  }

  render() {
    return <GalleryTemplate photos={this.state.photos} />
  }
}

export default GardenArchitecture
